@-webkit-keyframes pop-in {
  0%  { 
    -webkit-transform: scale(.5, .5); 
            transform: scale(.5, .5);
    opacity: 0; 
  }
  40% { opacity: 1; }
  65% { -webkit-transform: scale(1.1, 1.1); transform: scale(1.1, 1.1); }
  100% { -webkit-transform: scale(1, 1); transform: scale(1, 1); }
}

@keyframes pop-in {
  0%  { 
    -webkit-transform: scale(.5, .5); 
            transform: scale(.5, .5);
    opacity: 0; 
  }
  40% { opacity: 1; }
  65% { -webkit-transform: scale(1.1, 1.1); transform: scale(1.1, 1.1); }
  100% { -webkit-transform: scale(1, 1); transform: scale(1, 1); }
}

@-webkit-keyframes beat {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  25% {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
  }
  40% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  60% {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes beat {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  25% {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
  }
  40% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  60% {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

#root {
  width:80%;
  margin:auto;
}

html {
  background: url(img/poohbg.jpg) no-repeat center center fixed;
  background-size: cover;
}

body {
  font-family: 'Gaegu', cursive;

}

ol, ul {
  padding-left: 30px;
}

.overflow {
  overflow:hidden;
}

.hide {
  display:none;
}

.test {
  background-color: white;
  padding: 20px;
  text-align: center;
  border: 6px solid purple;
  width:200px;
  height:200px;
  margin:auto;
  margin-top:-10%;
  z-index: 4;
  -webkit-animation-name: pop-in;
          animation-name: pop-in;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}


.test2 {
  z-index:1;
}

.container {
    cursor: -webkit-grab;
    cursor: grab;
    position: relative;
    height: 125px;
    width: 100px;
    -webkit-perspective: 600;
            perspective: 600;
    float:left;
    -webkit-transition: -webkit-transform 2s ease-in-out;
    transition: -webkit-transform 2s ease-in-out;
    -o-transition: transform 2s ease-in-out;
    transition: transform 2s ease-in-out;
    transition: transform 2s ease-in-out, -webkit-transform 2s ease-in-out;
    margin:10px;
}


.check:hover {
  -webkit-transform: rotateX(20deg);
          transform: rotateX(20deg);
  -webkit-box-shadow: 0 10px 10px rgba(50,50,50,.2);
          box-shadow: 0 10px 10px rgba(50,50,50,.2);
}


.card {
   height: 100%;
   width: 100%;
   position: absolute;
   -webkit-transform-style: preserve-3d;
           transform-style: preserve-3d;
   -webkit-transition: all 1s ease-in-out;
   -o-transition: all 1s ease-in-out;
   transition: all 1s ease-in-out;
   border-radius:5%;


}

.card img {
  width:100px;
  height:125px;
  border-radius:5%;

}

.flipcard {
    -webkit-transform: rotateX(180deg);
            transform: rotateX(180deg);
}

.side {
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    border: 3px solid #ddf3ec;
    border-radius:5%;
    height: 100%;
    position: absolute;
    overflow: hidden;
    width: 100%;
}

.back {
    -webkit-transform: rotateX(180deg);
            transform: rotateX(180deg);
    cursor: auto !important;

    border:0px !important;
    -webkit-box-shadow: 1px 2px 4px lightslategray;
            box-shadow: 1px 2px 4px lightslategray;
}

.matched .back img {
  cursor:auto;
  -webkit-animation: 1s 1s beat;
          animation: 1s 1s beat;
}

.game {
  width:75%;
  margin: auto;
  padding:50px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.greyed {
  opacity:0.6;
  z-index:-2;
}

.instructions {
  color:white;
  text-align:center;
  font-size:20px;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}


/* On screens that are 400px or less */
@media screen and (max-width: 400px) {

    #root {
    width:100%;
  }
    .container {
      width: 66px;
      height: 83px;
      margin:5px;
  }

    .card img {
    width:66px;
    height:83px;
  }

  .board-row {
    padding-top:30px;
    padding-left:15px;
  }
  .game {
    width:250px;
    padding:0px; 
  }

}

/* On screens that are 400px or more */
@media screen and (min-width: 400px) {

  #root {
  width:100%;
}

  .container {
    width: 66px;
    height: 83px;
    margin:5px;
}

  .card img {
  width:66px;
  height:83px;
}

.board-row {
  padding-top:30px;
  padding-left:15px;
}

.game {
  width:335px;
  padding:20px;
}

}

/* On screens that are 600px or more */
@media screen and (min-width: 600px) {

  .container {
    width: 83px;
    height: 104px;
    margin:5px;
}

  .card img {
  width:83px;
  height:104px;
}

.board-row {
  padding-top:15px;
  padding-left:15px;
}

.game {
  width:400px;
  padding:20px;
}

}

/* On screens that are 900px or more */
@media screen and (min-width: 900px) {

  .container {
    width: 100px;
    height: 125px;
    margin:10px;
}

  .card img {
  width:100px;
  height:125px;
}

.board-row {
  padding-top:15px;
  padding-left:15px;
}

.game {
  width:500px;
  padding:20px;
}

}

@media screen and (min-width: 1200px) {

  .container {
    width: 120px;
    height: 150px;
    margin:10px;
}

  .card img {
  width:120px;
  height:150px;
}

.board-row {
  padding-top:15px;
  padding-left:15px;
}

.game {
  width:600px;
  padding:20px;
}

}




